(function() {
  'use strict';

  angular
    .module('cakeFedTest', ['ngResource', 'ngRoute', 'ui.bootstrap']);

})();

(function() {
  'use strict';

  angular
    .module('cakeFedTest')
    .controller('RegisterController', RegisterController);

  /** @ngInject */
  function RegisterController($log) {
    var vm = this;
    vm.registrationComplete = false;

    vm.register = function (form) {
      $log.info(vm.user);
      $log.info(form);
      if(vm.user.firstName.length > 10) {
        form.firstName.$setValidity("randomError",false);
      } else {
        vm.registrationComplete = true;
      }

    }

  }
})();

(function() {
  'use strict';

  angular
    .module('cakeFedTest')
    .directive('acmeNavbar', acmeNavbar);

  /** @ngInject */
  function acmeNavbar() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/navbar/navbar.html',
      scope: {
          creationDate: '='
      },
      controller: NavbarController,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    /** @ngInject */
    function NavbarController(moment) {
      var vm = this;

      // "vm.creationDate" is available by directive option "bindToController: true"
      vm.relativeDate = moment(vm.creationDate).fromNow();
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('cakeFedTest')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
    var vm = this;
    vm.creationDate = 1474371397023;

  }
})();

(function() {
  'use strict';

  angular
    .module('cakeFedTest')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('cakeFedTest')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/register', {
        templateUrl: 'app/components/register/register.html',
        controller: 'RegisterController',
        controllerAs: 'register'
      })
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'main'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

/* global moment:false */
(function() {
  'use strict';

  angular
    .module('cakeFedTest')
    .constant('moment', moment);

})();

(function() {
  'use strict';

  angular
    .module('cakeFedTest')
    .config(config);

  /** @ngInject */
  function config($logProvider) {
    // Enable log
    $logProvider.debugEnabled(true);
  }

})();

